.experiences{
    display: flex;

}

.experience{
    width: 420px;
    /*452*/
    /*min-height: 620px;*/
    min-width: 270px;
    /*650*/
    color: white;
    border-radius: var(--borderRadius);
    margin: 0 20px;
    /*aspect-ratio: 452/650;*/
    z-index: 4;
    & h2.title{
        padding-left: 10px;
    }

    .textContainer{
        padding: 0 10px  10px 10px;
        display: flex;
        flex-direction: column;
    }

    .date{
        font-size: larger;
        letter-spacing: 2px;
        display: flex;
    }
    .title{
        margin: 10px 0;
    }

    .type{
        font-size: larger;
        letter-spacing: 2px;
    }

    .status{
        display: flex;
        border-radius: var(--borderRadius);
        color: black;
        background-color: #eedbc882;
        align-items: center;
        padding: 0 10px;
        text-align: center;
    }

    .description{
        text-align: justify;
        line-height: 1.4;
        /*min-height: 200px;*/
        font-size: larger;
    }

}



.companyLogoContainer{
    background-image: radial-gradient(#ffdabf, transparent);

    height: fit-content;
    display: flex;
    border-radius: var(--borderRadius);
    padding: 20px;
    justify-content: center;
    max-width: 100%;
    /*aspect-ratio: 1/1;*/
    box-shadow: 0px 5px 7px -6px black;
    >img{
        max-width: 100%;
        height: auto;
    }
}


.experiencesContainer{
    flex-direction: row;
    max-width: 100%;
    flex-wrap: wrap;
    margin-top: var(--cardContainerMargin);
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-items: center;
    justify-content: center;
}

.experienceHeader{
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    >p{
        margin: 2px 0;
        font-size: larger;
        color: rgba(255,255,255,0.9);
    }
}




.location{
    color: rgba(255,255,255,0.8);
    margin: 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &:before{
        content: " ";
        width: 20px;
        height: 20px;
        display: inline-block;
        margin-right: 5px;
        background-image: url("/public/img/pin.png");
        background-size: cover;
    }
}

