.glass{
    backdrop-filter: blur(25px) saturate(120%);
    width: 100%;
    height: 100%;
    border-radius: var(--borderRadius);
    /*border: 1px solid var(--colGray1);*/
    display: flex;
    flex-direction: column;
    z-index: 5;
    /*background-color: #5c5b632b;*/
    /*background-image: ;*/
    /*background-size: 160px 160px;*/
    background: 160px 160px url("/public/img/white_noise.png");
    /*, 100% 100% radial-gradient( white , transparent) no-repeat*/
    /*background-position: ;*/
    /*opacity: 0.1;*/
    box-shadow: 0px 0px 11px -10px #ffffff inset, 0px 0px 12px -8px black;
}

.lightGlass{
    background-color:  linear-gradient(var(--lightGlass),black);
}

.movingBackground {
    /*background-image: var(--movingColors);*/
    background-repeat: round;
    background-attachment: fixed;
    background-origin: content-box;
    border: inset 0px black;
    transition: 0.5s linear;
    /*box-shadow: inset 0 0 0 0px rgba(0,0,0,0);*/
    background-size: contain;

}

