.workTechnology{
    display: flex;
    min-width: 50px;
    height: 30px;
    background-color: rgb(0 0 0 / 10%);
    border-radius: 20px;
    padding-left: 10px;
    padding-right: 10px;
    letter-spacing: 2px;
    font-feature-settings: "pcap", "c2pc";
    font-variant: all-petite-caps;
    text-align: center;
    justify-content: center;
    margin: 0 5px 5px 0;
    align-items: center;
    /*box-shadow: 0px 5px 7px -6px #0000006b;*/
    box-shadow: -2px 4px 7px -6px #000000 inset, 3px -2px 7px -6px #ffffff70 inset;
    transition: all 0.5s;

    &:hover{
        filter : brightness(110%);
        box-shadow: 0px 5px 7px -6px #0000006b, -2px 3px 12px -6px #ffffff4a inset, 2px -3px 12px -6px #000000a1 inset;
        transform: translateY(-1px) scale(1.02);
        transition: all 0.2s;
        cursor: default;
        color: white;
        background-color: var(--lightGlass);
    }
}

