/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    color: white;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #73717152;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: var(--borderRadius);
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: #ffffff52;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: var(--borderRadius);
}

input:checked + .slider {
    background-color: var(--CleanBlack);
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}
.toggleSwitch{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}

.toggleSwitch>span{
    color: rgba(255, 255, 255, 0.62);
    padding-left: 5px;
}